import Head from 'next/head'
import { useState } from 'react'
import 'react-responsive-carousel/lib/styles/carousel.min.css'
import { Carousel } from 'react-responsive-carousel'
import '../styles/app.styl'
import '../public/css/fontello.css'
import me from '../me.json'
const nav = ['contact', 'work', 'about']

export default function Home() {
  const [active, setActive] = useState(nav[0])
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)
  return (
    <div>
      <Head>
        <title>{me.title}</title>
        <link rel="icon" href="/favicon.ico" />
        <link href="http://fonts.googleapis.com/css?family=Raleway:400,700" rel="stylesheet" type="text/css"></link>
        <link href="http://fonts.googleapis.com/css?family=Oswald" rel="stylesheet" type="text/css"></link>
      </Head>

      <h1>{me.name}</h1>
      <div className="mobile">
        <div className="button" onClick={() => setMobileMenuOpen(!mobileMenuOpen)}>
          <i className="icon-menu"></i>
        </div>
        <div className="name">{me.name}</div>
      </div>
      <div id="app">
        <div className="overlay"></div>
        <div className={`navigation ${mobileMenuOpen ? 'open' : ''}`}>
          <ul>
            <span className={`slider ${active}`}></span>
            {nav.map((view) => (
              <li
                key={view}
                onClick={() => {
                  setActive(view)
                  setMobileMenuOpen(false)
                }}
                onMouseEnter={() => !mobileMenuOpen && setActive(view)}
              >
                {view}
              </li>
            ))}
          </ul>
        </div>
        <div className="main">
          <div className="mobile-subheader">{active}</div>
          <div className="circle"></div>
          <div className={`spinner ${active}`}>
            {nav.map((view) => (
              <div key={view} className={`button ${view}`} onClick={() => setActive(view)}>
                <div className="info">
                  <div>{view}</div>
                </div>
              </div>
            ))}
          </div>
          <div className="wrapper">
            <div className="contents">
              <div className={`content contact ${active === 'contact' ? 'active' : ''}`}>
                {me.contacts.map((contact) => (
                  <div key={contact.icon}>
                    <a className={contact.icon} href={contact.url} target="_blank">
                      <i className={`icon-${contact.icon}`}></i>
                      {contact.icon === 'file-pdf' && <div className="resume">Resume</div>}
                    </a>
                  </div>
                ))}
              </div>
              <div className={`content work ${active === 'work' ? 'active' : ''}`}>
                <Carousel
                  showThumbs={false}
                  showArrows={true}
                  showIndicators={false}
                  infiniteLoop={true}
                  renderArrowPrev={(onClickHandler, hasPrev, label) =>
                    hasPrev && (
                      <div className="button left" onClick={onClickHandler}>
                        <i className="icon-left-open"></i>
                      </div>
                    )
                  }
                  renderArrowNext={(onClickHandler, hasNext, label) =>
                    hasNext && (
                      <div className="button right" onClick={onClickHandler}>
                        <i className="icon-right-open"></i>
                      </div>
                    )
                  }
                >
                  {me.work.map((project) => (
                    <div key={project.image}>
                      <img src={project.image} />

                      <div className="legend" style={{ opacity: 0.9 }}>
                        <h3>{project.title}</h3>
                        <div>{project.subtitle}</div>
                      </div>
                    </div>
                  ))}
                </Carousel>
                {/* <div className="carousel-controls">
                                    <div className="button left">
                                        <i className="icon-left-open"></i>
                                    </div>
                                    <div className="button right">
                                        <i className="icon-right-open"></i>
                                    </div>
                                </div> */}
              </div>
              <div className={`content about ${active === 'about' ? 'active' : ''}`}>
                <img src="/images/me.png" />
                <div>tea drinking javascript yogi</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
